        <el-collapse-item title="真正优秀的人，没时间跟生活抬杠" name="1">
          <div style="text-indent: 2em;">
            人们常说，你未来的模样，就藏在你现在的努力里。很喜欢这样一句话：生活从来没有捷径，当别人为了梦想默默耕耘的时候，只是你没看到而已。真正优秀的人，没时间跟生活抬杠。是的，真正优秀的人，从来不会抱怨生活，也不会为自己的懒惰找借口。
            01
            喜欢抱怨周末回老家，碰到了许久不见的中学同学小丽，没说两句话，小丽就开始给我抱怨自己老公的种种，说闺蜜的老公开着豪车，住着大房子，闺蜜每天除了购物就是出去玩。而自己的老公每月就那么一点死工资，就知道省钱，买菜的时候还总跟有时候竟为了一块钱跟人家理论半天，“你知道吗？阿心，我真是受够了”。看着小丽一脸嫌弃丈夫的表情，我笑了小丽的老公是一个老实本分的男人，除了钱挣的少，对小丽那是真心的好。还记得当初小丽经常在我们耳边说，老公如何对她好，恨不得让所有人都知道她嫁了个好老公。可现在结婚还不到两年，她就开始各种嫌弃，面对小丽的嫌弃，她老公也只是呵呵一笑，还是生活中，我们常常会碰到一些负能量的人，一开口就是对生活的各种抱怨，就连自己最亲近的人，也在外人面前毫不保留的说道。看起来，她抱怨的是生活中的一些琐事，是爱人的一些生活细节。从本质上来说，她美国心灵导师威尔·鲍温在《不抱怨的世界》书中说道：“抱怨的本质，其实是为了获取同情心和注意力，以及避免去做一个真正优秀的人，从来不会抱怨生活。大多数人的抱怨，都是因为自己不够优秀，往往你的话里02
            当我们羡慕别人的好运时，却看不到他在图书馆日夜苦读；嫉妒别人身材好，却不知他很喜欢演员胡歌，23岁那年一夜成名，24岁经历了车祸、毁容、重生，25岁回归娱乐圈，如今33岁的他，看上胡歌说：我幸运的是最先找回了生命，却看着另一个生命逐渐远去；我感谢上天保全了我的眼睛，却无法在感受光明的同时但是，伤疤不是他的缺陷，而是一枚勋章。很多人说，李逍遥是他的他说，回不去的皮囊还记得《琅琊榜》中的梅长苏，其实正是胡歌的另一个自己，经历过黑暗，却依然能够淡定从容，清澈坚定。就如戏里梅长苏说的一样：“既然我活了下来，就不能白活着”。即便是受过伤，也可法国作家罗曼·罗兰说：世界上只有一种英雄主义，就是看清生活的真相之03
            真正优别整天抱怨生活对你怎样不好，你不努力，上天又怎会眷顾你。人生路上，速度并不重要，重要的是耐力。真正优秀的人，没有时间与选择纠结，只能拼命往前冲，很多事，很喜欢心灵作家张德芬的一句话：天下只有三种事：我的事，他的事，老天的事。抱怨自己的人，应该试着学习接纳自己；抱怨他人的人，应该试着把抱怨转成请求；抱怨老天的人，请试着用祈祷的方这样一来，你的生活会有想象不到的转变，你的人生也会更加地美好、圆满。希望那时候，我们都没有早一步，也没有晚一步的，就在那时候，刚好遇到最优秀的你。
