        <el-collapse-item title="有种脾气叫，不放弃" name="2">
          <div style="text-indent: 2em;">有句话说：人生没有四季，只有两季，努力就是旺季，不努力就是淡季。
            这句话既是一种积极的生活态度凡是优秀的人，都有01
            前两天，认识多年的好友李总病倒了，30岁，年轻李总经营着一家网络科技公司，创业三年。这三年来，他一直拼命工作，每天早上7点到公司，凌晨12才走。他不仅资助了许多贫困山区的孩子，还拿出自己的积蓄成立了创业基金，专门扶在我的印象中，李总是一个不放弃、不认输人，他之所以这么做，源于他心中的一个梦。他出生在一个贫困的山村，小时候家里经常不通电，每次上学要走好几公里的山路。每当他借着微弱的烛光看书时，心里总有一个声音告诉他，你一定要努力，改变自就是在这样的环境下，他凭着自身的坚韧和不服输，大学毕业后，成立了自己的公司。就在公司步入正轨的在治疗期间，他也没有放弃工作。用他的话说：我还有梦，我不能倒。哪怕我的生命只剩一天，也要帮人生就是这样，这个世界不会辜负每一份坚持和努力，时光也不会怠慢每一个勇敢追逐梦想的人，只要你不放弃，02
            宁愿笑着流有时候，我们不得不面对那些你觉得无法做到的事情，痛苦、前段时间，被一部励志短片《永不放弃》中的一幕所震撼。短片讲述了一支球队在教练的指挥下做一项“死亡爬行”的体能训练，一人半卧，膝骨不能着地，背上再压一个人，沿着球场爬50米，所有队员都认为这是不可能完成的任务，在中途不到这时候，教练叫出了球队中较优秀的队员布洛克，让他再做一次。他在布洛克的眼睛上蒙上一块布，希望他能布洛克爬行时，教练始终在鼓励，喊了13
            次 “对了” 、15 次 “加油” 、 23 次 “别放弃” 、 3 次 “不要停”、 48 次 “继续” …… 教练告诉布洛克，不要浪费上帝赐予在教练的激励下，布洛克不仅爬行了
            50米，他还背着一个80公斤的队友爬完了全程100米。一个普通人释放了自己最大的能量，坚持到底不愿放因为不放弃，看似不可能的事变成了奇迹。因为不放弃，很多平凡人战爱迪生说：我们最大的弱点在于放弃，成功的必然之路就是不断的重来一次。宁愿笑着流泪，03
            生活中，我们经常会出现这样的念头，“太累了”、“算了吧”
            ……但是，更多的时候，你在奋斗路上是孤独的、寂寞的，你需要不断告诉自己，为了梦想，一定要坚持，不要放弃，很喜欢演员彭于晏，这个在30岁前一直不温不火的年轻人，直到2010年凭借电影《翻滚吧！阿信》，他为了演好这个角色，从未接触过体操的他开始进行魔鬼式训练，每天训练十几个小时，为了符合角色定位，他只能吃不加终于在他的咬牙坚持下，获得了金马奖男主角提名，正是他的不认输，不放弃，成就像电影中的一句台词：如果你一生只有一次翻身的机也许我们并不知道这一次机会何时出现，但我们必须咬牙撑过那些看似黑很喜欢网上的一段话：每个优秀的人，都有一段沉默的时光，那段时光，是付出了很多努力，却得不到结果的日子，生活中多数的错失，是因为不坚持，不努力，不挽留，然后骗自己说一切都是命运。或许我们现在很迷茫，或许不知道往哪一个方向走，但生活本身就是寻找自己的过程，我们不用为了天亮去跑，只要不放弃，跑下去，天自己会亮。
