        <el-collapse-item title="不甘于平庸，那就做一只跳出温水的青蛙" name="3">
          <div style="text-indent: 2em;">
            池塘里的一只青蛙被人捉住后放置在温水缸里，舒适的环境让它欣喜不已，不用躲避风雨的侵袭，不必担心阳光的炙烤，它心安理得的沉溺于温暖的水中，梦想早已抛诸脑后，它轻盈的身躯变得臃肿不堪、灵活的双脚变得笨拙，直到有一天，它意识到自己即将成为人类的盘中餐，于是后悔万分。
            它思念池塘里的泥泞、荷叶和伙伴，向往驰骋于天地间的无拘无束，痛定思痛之后，它开始了跳跃，一次次艰难的尝试之后，终于蹦出了水缸，重新捡起了属于一只青蛙的梦想，尽管前途未卜，但是作为一只跳出温水的青蛙，关于梦想，从小时候的豪言壮语到现世的知足常乐，人生的种种遭际将孩子们的英雄梦、公主梦无情地击碎，所以我们的梦想变得越来越小，从科学家、文学家到律师、教师，再到为房贷、车贷、生计而奔波的渺小的蚁族。当那个叫梦想的东西从我们的世界里消失，便会无奈、颓废地说一声：“谈梦想太奢侈了。”然后对那些执着地坚守自己如果不是我们嘲笑的那个人已经长成了一棵参天大树，如果不是人生突遭变故、如果不是感受到岁月匆匆，有谁会痛下决树立梦想也许是随口一说，也许是深思熟虑，但是重拾梦想需要何等的勇气与决然，抛掉安逸、戒掉拖延、摒弃惰性，那意味着一努力重拾梦想，从打破原先舒适的生活方式开始。重拾梦想是崭新人生的开始，梦想的实现需要积极有效的行动，因此，我们必须毅然与此刻安逸的生活告别，行动力是开启崭新人生的第一步，重新起步的过程真的很难，但是，努力了，人生还有一线希望，原地踏步破旧立新，打破旧的生活习惯与思维模式，建立新的人生秩序，建构阶梯式的成长发展规划，脚踏实地，从踏上第一级台阶开始，一步一步重拾梦想，就要把他人的眼光、世俗的偏见抛到九霄云外。重拾梦想的人比起初涉职场的新人存在许多劣势，技艺已经生疏、已不再年轻、柴米油盐仍需要操心，但是这些小青年口里的大叔、阿姨对自己狠起来是旁人所不及的，因为有过惨痛的经历、因虚度的年华而悔恨过、懂得明白笨鸟先飞的道理，他们会抓住一切可以提升自我的机会苦练技能、钻研业务，就像上高三时，班上转来一个新同学，是一个三十多岁的女人，她准备和我们一起参加高考，当她被同学们团团围住，她没有不好意思，而是落落大方地向我们诉说着她的大学梦，年轻时，为了减轻家里的负担，本想考大学的她顶替父亲在煤矿上班，现在，家境好了，单位可以脱产学习，梦想再一次燃起，家人说：“都是孩子妈了，瞎折腾什么”，在同事眼里她是别样的存在，但是她还是毫不犹豫地拿起了书本，她向比她小的学生提问，她和同学们一许多年过去了，不知她过得怎么样了，但是，她的人生肯定会因为这次独特重拾梦想，就要从现在开始，磨练自己的专业技能。技能是一个人在职场上赖以生存的资本，在电视剧《第二次也很美》中，一毕业就做全职太太的安安初入职场，因为技能的生疏耽误了整个工作的进度，遭到同事们的排挤，她想尽各种办法缓解同事之间的关系，但是许朗却告诫她提高业务水平才是最关键的，休息时间她重拾梦想，享受追梦的过程，因为追梦本身就是一种成长。我不知道坚持下去能否成功，但是我知道不坚持就会一无所有。不是所有的梦想都能实现，但是实现梦想的过程对自己来说是一种历练、一种提升，在这期间磨练的技能也会在未来如果不愿沉溺于现实的安逸，那就做一只跳出温水里的青蛙。余生未可知，庸庸碌碌也好、奋起直追也好，愿你不要辜负似水流年。
          </div>
