        <el-collapse-item title="你需要的不是说出来，而是去做！" name="4">
          <div style="text-indent: 2em;">最近一个朋友再次跟我说自己口才不行，这已经是他第n次跟我谈到这件事情了。
            在社会中生活，沟通是必须的，那么如何沟通，就显得尤为重要。几乎是每个人都遇到过无话可说的情况，也有很多人有社交恐惧症。每个人都是独立而有个性的，不能与每个陌生人畅谈也但是社交作为每个人必备的技能，哪怕不能畅谈，至少也应该做到正常的交流，而不是遇到陌生人就不知从何谈起，甚我这个朋友无数次的跟我说起自己口才不行，听多了，我首先，不要总是说自己这不行，那不行，比如你口才不行，你完全可以说自己在语言表达方面有待提高，并且会在这个方面多下功夫。你要委婉的说出自己的弱点，我现在非常不喜欢一个人说完自己这不行那不行之后，并没有做出改变，还是按照自己原来的方式继续生活。既然知道自己的弱点，如若不做出改变，那么你把自己的弱点赤裸裸的表达出来，是为了求可怜吗？并不是每个人都能包容你，也不会认人都同情你，并从小，我就是一个不太爱说话，也不太会说话的人，生活中也尽量避免与其他人交流。虽然意识到自己在这方面有欠缺，但是得过且过，也并没有把不善言谈当后来，因为交流障碍，也错失了很多的机会。在一次次的懊恼与悔恨中，觉得自己不能再这样下去了。言谈在人际交往中是最基本的事情，不能因为自己的胆小与懦弱就放弃社交。更何况人生那么长，总不能于是在初中时期，开始读大量提升口才的书籍，扩大自己的知识面，学习说话的技巧。平时也更加注重自己的言谈举止，在一次次的社交中总结经验，下次交谈的过久而久之，就不会再怕与别人交谈，也知道如何找话题，面对不同的现在最怕的就是，一个人意识到自己的不足，但是因为懒，因为安于现状很多人就会想，我虽然有毛病，但是我现在但是，很多事情，你不去主动挑战它，每个人都有自己擅长的领域，也有不擅长的事情。并不是说改变，就是要做到完美，做到无双。而是将不擅长的事你可能不会引经据典
            口灿莲花，但是可以做到正常的交流，并且不惧怕交流。这又标准因人而异，不需要给自己制定太高的标准，然后望洋兴叹，还没有做出改变之前，我们需要制定合适的目标，然后通过努力，去达到及格。要是能做到面对不擅长的事情，要么彻底放弃，说服自己的内心要么就去做出改变，并且坚持的努力，成千万不要抱怨，并且抱要有接受现实的心态，也要有敢于跳出舒适区的勇气。人生还长，要成为更好的自己。
