import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

// VueRouter原型上加抛出异常,关于重复点击同一个路由出现的报错问题解决
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function(location) {
  return originalPush.call(this, location).catch(() => { })
}
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/home'
  },
  {
    path: '/home',
    name: 'Home',
    component: Home
  },
  {
    path: '/hometown',
    component: () => import(/* webpackChunkName: "about" */ '../views/Hometown.vue')
  },
  {
    path: '/college',
    component: () => import(/* webpackChunkName: "about" */ '../views/College.vue')
  },
  {
    path: '/item1',
    component: () => import(/* webpackChunkName: "about" */ '../views/Item1.vue')
  },
  {
    path: '/item2',
    component: () => import(/* webpackChunkName: "about" */ '../views/Item2.vue')
  },
  {
    path: '/item3',
    component: () => import(/* webpackChunkName: "about" */ '../views/Item3.vue')
  },
  {
    path: '/msg',
    component: () => import(/* webpackChunkName: "about" */ '../views/Msg.vue')
  }
]

const router = new VueRouter({
  routes
})

export default router
